<template>
  <div>
    <div class="navbar-div">
      <van-nav-bar title="" fixed="true" left-arrow @click-left="onClickLeft" />
    </div>
    <div class="topimage-div">
      <img :src="goodsInfo.IMAGE1" width="100%" />
    </div>

    <van-cell-group>
      <van-cell>
        <div class="goods-title">{{goodsInfo.NAME}}</div>
        <div class="goods-price">{{goodsInfo.ORI_PRICE}}</div>
      </van-cell>
      <van-cell class="goods-express">
        <van-col span="10"> 0</van-col>
        <van-col span="14">{{goodsInfo.AMOUNT}}</van-col>
      </van-cell>
    </van-cell-group>

    <div>
      <van-tabs swipeable sticky>
        <van-tab title="">
          <div class="detail" v-html="goodsInfo.DETAIL"></div>
        </van-tab>
        <van-tab title=""></van-tab>
      </van-tabs>
    </div>

    <div class="goods-bottom">
      <van-goods-action>
        <van-goods-action-icon icon="chat-o" text="" />
        <van-goods-action-icon icon="cart-o" text="" :info="carNumber" @click="OnClickCart" />
        <van-goods-action-button type="warning" text="" @click="addToCart" />
        <van-goods-action-button type="danger" text="" @click="onClickButtonBuy" />
      </van-goods-action>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import url from "@/serviceAPI.config.js";
import { Toast } from "vant";
import { Dialog } from "vant";
import store from "../store/index";
import { mapState, mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      goodsId: "",
      count: 1,
      isLogin: false,
      carNumber: "",
      goodsInfo: {} //
    };
  },
  // filters: {
  //   moneyFilter(money) {
  //     return toMoney(money);
  //   }
  // },

  computed: {
    ...mapState({
      selectGoods: state => state.selectGoods, //store.selectGoodsthis.selectGoods
      count: state => state.count //store.selectGoodsthis.selectGoods
    })
  },
  methods: mapActions(["increment", "decrement"]),
  created() {
    if (this.$store.getters.isAutnenticated) {
      this.isLogin = true;
      this.carNumber = this.$store.state.count;
    }
    this.goodsId = localStorage.goodsId;
  //  console.log("localStorage.goodsId :", localStorage.goodsId);

    this.getInfo();
  },
  methods: {
    onClickButtonBuy() {
    //  console.log("this.goodsInfo :", this.goodsInfo);
      let data = {};
      data.name = this.goodsInfo.NAME;
      data.price = this.goodsInfo.ORI_PRICE;
      data.count = 1;
      data.smpic = this.goodsInfo.IMAGE1;
      localStorage.setItem("url", "goods");
      localStorage.setItem("goodsPay",JSON.stringify(data));
      this.$router.push("/Order");
    },
    OnClickCart() {
      this.$router.push("ShopCar");
    },
    getInfo() {
      this.$axios
        .post("goods/getDetailGoodsInfo", {
          goodsId: this.goodsId
        })
        .then(response => {
          if (response.data.code == 200 && response.data.message) {
            this.goodsInfo = response.data.message;
          } else {
            Toast("");
          }
        //  console.log(this.goodsInfo);
        })
        .catch(error => {
        //  console.log(error);
        });
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    addToCart() {
      if (this.isLogin) {
        console.log(this.selectGoods);
        let flag = true;
        let num = 0;

        this.selectGoods.forEach((item, index) => {
          console.log(this.goodsId);

          // kid,
          if (this.goodsId == item.goodsId) {
            flag = false;
            item.count += this.count;

            // this.isShow = !this.isShow;

            this.upDateCount();
          }
        });
        if (flag) {
          this.$store.dispatch("increment");
          this.carNumber = this.$store.state.count;
          //   console.log('getCarNumber :', this.$store.commit('ADD_CAR_NUMBER'));
          console.log(
            "this.$router.state.carNumber :",
            this.$store.state.count
          );
          this.$store.dispatch("addGoods", {
            name: this.goodsInfo.NAME,
            price: this.goodsInfo.ORI_PRICE,
            smpic: this.goodsInfo.IMAGE1,
            goodsId: this.goodsId,
            count: this.count,
            check: true
          });

          this.upDateCount();
          this.isShow = !this.isShow;
        }
      } else {
        Dialog.alert({
          message: ""
        }).then(() => {
          this.$router.push("/Login");
          // on close
        });
      }

      //  this.$router.push({ name: "ShopCar" });
    },
    //
    upDateCount() {
      let num = 0;
      this.selectGoods.forEach((item, index) => {
        num += item.count;
      });
      Toast.success("");
      this.cartCount = String(num);
    }
  }
};
</script>

<style lang="less">
.topimage-div {
  margin-top: 1.5rem;
}
.goods {
  padding-bottom: 50px;
  &-title {
    font-size: 16px;
  }
  &-price {
    color: #f44;
  }
  &-express {
    color: #999;
    font-size: 13px;
    padding: 5px 15px;
  }
  &-cell-group {
    margin: 15px 0;
    .van-cell__value {
      color: #999;
    }
  }
}
.goods-name {
  background-color: #fff;
}
.goods-price {
  background-color: #fff;
}
.detail {
  font-size: 0px;
}
.goods-bottom {
  position: fixed;
  bottom: 0px;
  left: 0px;
  background-color: #fff;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-flow: nowrap;
}

.goods-bottom > div {
  flex: 1;
  padding: 5px;
}
</style>


