<template>
  <div class="cart">
    <div class="isLogin" v-if="!isLogin">
      <p></p>
      <van-button
        color="linear-gradient(to right, #4bb0ff, #6149f6)"
        round
        class="button"
        @click="login"
      ></van-button>
    </div>
    <!---->
    <div v-else>
      <van-row class="navbar">
        <van-col span="8" offset="10" class="navbar-title"></van-col>
        <van-col span="6" offset>
          <van-button round type="primary" size="small" v-show="show" @click="deletegoods"></van-button>
        </van-col>
      </van-row>

      <div class="cart-section" v-if="goods!=0">
        <van-checkbox-group class="card-goods" v-model="checkedGoods">
          <van-checkbox
            class="card-goods__item"
            v-for="item in goods"
            :key="item.goodsId"
            :name="item.goodsId"
          >
            <van-card :title="item.name" :price="parseInt(item.price)" :thumb="item.smpic" />
          </van-checkbox>
          <ul class="steper">
            <li v-for="(item,index) in goods " :key="index">
              <van-stepper v-model="item.count" />
            </li>
          </ul>
        </van-checkbox-group>

        <van-submit-bar
          :price="totalPrice"
          :disabled="!checkedGoods.length"
          :button-text="submitBarText"
          @submit="onSubmit"
        >
          <van-checkbox v-model="allChecked" @click="checkall"></van-checkbox>
        </van-submit-bar>
      </div>
      <!---->
      <div class="cart-empty" v-else>
        <img src="../assets/images/empty.png" alt />
        <div class="title">~</div>
        <router-link to="/home" class="goHome"></router-link>
      </div>
    </div>
  </div>
</template>

<script>
import store from "../store/index";
import { mapState } from "vuex";
import { Col } from "vant";
//import {eventBus} from '../../assets/js/eventBus'

//import { Checkbox, CheckboxGroup, Card, SubmitBar } from 'vant';
export default {
  //components: {
  //  [Card.name]: Card,
  //  [Checkbox.name]: Checkbox,
  //  [SubmitBar.name]: SubmitBar,
  //  [CheckboxGroup.name]: CheckboxGroup
  //},
  store,
  data() {
    return {
      checkedGoods: [],
      goods: [],
      isLogin: false, //
      allChecked: false, //
      flag: true,
      show:false //
    };
  },
  watch: {
    //
    checkedGoods(e, old) {
      if (e.length!=0) {
        this.show = true
      }else{
        this.show = false
      }
      console.log('this.show :', this.show);
      if (this.goods.length == e.length) {
        this.allChecked = true;
      } else {
        this.allChecked = false;
      }
    }
  },
  created() {
    if (this.$store.getters.isAutnenticated) {
      this.isLogin = true;
    }
  },
  mounted() {
    this.goods = this.selectGoods;
    //console.log(this.selectGoods);
  },
  methods: {
    login() {
      this.$router.push("/Login");
    },
    deletegoods() {
      let deletearr = [];
      deletearr = this.checkedGoods;
    //  console.log(deletearr);
      for (let i = 0; i < deletearr.length; i++) {
         this.$store.dispatch("decrement");
        this.$store.dispatch("removeGoods", {
          goodsId: deletearr
        });
      }
  
      if ( this.goods.length==0) {
        this.show = false
      }
    },

    //
    checkall() {
      let arr = [];

      if (!this.allChecked) {
        this.selectGoods.forEach((item, index) => {
          arr.push(this.selectGoods[index].goodsId);
        });
        this.checkedGoods = arr;
      } else {
        this.checkedGoods = [];
        
      }
    },
    //
    onSubmit() {
      let payGoods = [];
      this.selectGoods.forEach((item, index) => {
        for (var i = 0; i < this.checkedGoods.length; i++) {
          if (this.checkedGoods[i] == this.selectGoods[index].goodsId) {
            payGoods.push(this.selectGoods[index]);
            //	        this.selectGoods.splice(index, 1);
            //  console.log("");
          }
        }
      });
      // console.log('payGoods :', payGoods);
           localStorage.setItem("url",  "shopCar");
          localStorage.setItem("payGoods",  JSON.stringify(payGoods));
         
      //    console.log(this.selectGoods);
     
       this.$router.push({ name: "Order"});
    }
  },
  computed: {
    submitBarText() {
      const count = this.checkedGoods.length;
      return "" + (count ? `(${count})` : "");
    },
    totalPrice() {
      //0
      return this.goods.reduce(
        (total, item) =>
          total +
          (this.checkedGoods.indexOf(item.goodsId) !== -1
            ? parseInt(item.price * 100) * item.count
            : 0),
        0
      );
    },
    ...mapState({
      selectGoods: state => state.selectGoods //store.selectGoodsthis.selectGoods
    })
  }
};
</script>

<style lang="less" scoped>
.isLogin {
  text-align: center;
  margin-top: 10rem;
}
.button {
  height: 80%;
  width: 50%;
}
.navbar {
  margin-top: 10px;
  height: 2rem;
}

.cart {
  .cart-section {
    .card-goods {
      background-color: #fff;
      position: relative;
      &__item {
        position: relative;
        padding: 10px 0 10px 2%;
        border-bottom: 1px solid #e5e5e5;

        .van-checkbox__input {
          top: 50%;
          left: 15px;
          margin-top: -10px;
          position: absolute;
        }
        .van-checkbox__label {
          margin: 0;
          width: 100%;
          padding-left: 35px;
          box-sizing: border-box;
          .van-card {
            background: #fff;
          }
        }
        .van-card__price {
          color: #f44;
        }
      }

      .steper {
        position: absolute;
        top: 0;
        right: 4%;
        li {
          height: 100px;
          padding-bottom: 20px;
          display: flex;
          align-items: flex-end;
        }
      }
    }
    .van-submit-bar {
      margin-bottom: 3rem;
      .van-checkbox {
        font-size: 0.26rem;
        padding-left: 0.15rem;
      }
    }
  }
  .cart-empty {
    padding: 1.5rem 5%;
    text-align: center;
    .cart-ico {
      .van-icon-cart {
        font-size: 0.5rem;
        color: #999;
      }
    }
    .goHome {
      margin-top: 2rem;
      color: white;
      background-color: #45c763;
      text-align: center;
      margin: 0 auto;
      padding: 0.5rem;
      width: 5rem;
      height: 1rem;
      line-height: 1rem;
      border-radius: 1rem;
    }
    .title {
      text-align: center;
      padding: 0.5rem;
    }
    .empty-warm {
      font-size: 0.32rem;
      margin-top: 0.3rem;
      color: #999;
    }
  }
}
</style>


