// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'

import router from './router'
import VueAwesomeSwiper from 'vue-awesome-swiper';
import 'swiper/dist/css/swiper.css'
import store from '../src/store';
import axios from './http';


axios.defaults.baseURL = 'http://39.108.165.164:3000/api/';
Vue.config.productionTip = false;
Vue.prototype.$addr = axios;

import {DropdownMenu, DropdownItem,ContactCard, ContactList, ContactEdit, AddressEdit, Area, Popup, AddressList, Dialog, icon, SubmitBar, Checkbox, Card, stepper, CheckboxGroup, Cell, CellGroup, Tabbar, TabbarItem, Button, Row, Col, Swipe, SwipeItem, Lazyload, List, NavBar, Sidebar, SidebarItem, Field, Tab, Tabs, PullRefresh, GoodsAction, GoodsActionIcon, GoodsActionButton } from 'vant';
Vue.config.productionTip = false
Vue.use(Button).use(Row).use(Col).use(Swipe).use(SwipeItem).use(Lazyload).use(AddressList).use(Popup)
  .use(VueAwesomeSwiper).use(List).use(Tabbar).use(TabbarItem).use(NavBar).use(Area).use(AddressEdit).use(DropdownMenu).use(DropdownItem)
  .use(Sidebar).use(SidebarItem).use(Field).use(Tab).use(Tabs).use(PullRefresh).use(GoodsAction).use(ContactCard).use(ContactList)
  .use(ContactEdit)
  .use(GoodsActionIcon).use(Cell).use(CellGroup).use(Card).use(stepper).use(SubmitBar).use(icon).use(Dialog)
  .use(GoodsActionButton).use(Checkbox).use(CheckboxGroup);
Vue.prototype.$axios = axios;
/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  components: { App },
  template: '<App/>'
})



