<template>
  <div class="address">
    <van-nav-bar title="" left-arrow @click-left="onClickLeft" />
    <div >
      <van-address-list v-model="chosenAddressId" :list="userAddress" @add="onAdd" @edit="onEdit" />
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      chosenAddressId: "1"
    };
  },
  computed: {
    ...mapState({
      userAddress: state => state.userAddress
    })
  },
  created() {
   // console.log("this.userAddress :", this.userAddress);
  },
  methods: {
    onAdd() {
      this.$router.push("/AddAddress");
     // console.log("add");
    },

    onEdit(item, index) {
   //   console.log("item :", item);
      this.$router.push({ name: "EditAdress", query: { item: item } });
    //  console.log(edit);
    },
    onClickLeft() {
     // console.log(" :", "");
      this.$router.go(-1);
    },
    onClickRight() {
   //   console.log(" :", "");
    }
  }
};
</script>

<style scoped>
.empty {
  margin-top: 10rem;
  font-size: 14px;
  color:darkgrey;
  text-align: center
}
</style>


