<template>
  <div>
    <!--  -->
    <div class="search-bar">
      <van-row>
        <van-col span="3">
          <img :src="locationlIcon" width="65%" class="search-icon" />
        </van-col>
        <van-col span="16">
          <input type="text" class="search-input" />
        </van-col>
        <van-col span="5">
          <van-button size="small " class="search-button"></van-button>
        </van-col>
      </van-row>
    </div>
    <!--  -->
    <div class="swiper-area">
      <van-swipe :autoplay="3000" indicator-color="#e16531" style="height: 10rem;">
        <van-swipe-item v-for="( banner ,index) in bannerPicArray" :key="index">
          <img v-lazy="banner.image" />
        </van-swipe-item>
      </van-swipe>
    </div>
    <!--  -->
    <div class="type-bar">
      <div v-for="(cate,index) in  category" :key="index">
        <img v-lazy="cate.image" width="90%" />
        <span>{{cate.mallCategoryName}}</span>
      </div>
    </div>
    <!--  -->
    <div>
      <img v-lazy="adBanner" width="100%" />
    </div>
    <!--  -->
    <div class="recommend-area">
      <div class="recommend-title"></div>
      <div class="recommend-body">
        <swiper :options="swiperOption">
          <swiper-slide v-for="(item,index) in recommend" :key="index">
            <div class="recommend-item">
              <img :src="item.image" alt width="80%" />
              <div>{{item.goodName}}</div>
              <div>{{item.price}}</div>
            </div>
          </swiper-slide>
        </swiper>
      </div>
    </div>
    <!--  -->
  <floor-component :floorData="floor1" :floorTitle='floorName.floor1'></floor-component>
  <floor-component :floorData="floor2" :floorTitle='floorName.floor2'></floor-component>
  <floor-component :floorData="floor3" :floorTitle='floorName.floor3'></floor-component>
  <!--  -->
  <div class="hot-area">
    <div class="hot-title"></div>
      <div class="hot-goods">
        <van-list>
          <van-row >
            <van-col span="12" v-for="(item,index) in hotGoods" :key="index">
              <goods-info :goodsImage="item.image" :goodsName="item.name" :goodsPrice="item.price"></goods-info>
            </van-col>
          </van-row>
        </van-list>
      </div>
    
  </div>
  </div>
</template>

<script>
import goodsInfo from '../components/goodsinfoComponent';
import "swiper/dist/css/swiper.css";
import floorComponent from '../components/floorComponent';
import { swiper, swiperSlide } from "vue-awesome-swiper";
import axios from "axios";
export default {
  data() {
    return {
      swiperOption: {
        slidesPerView: 3 //
      },

      category: [],
      adBanner: [],
      bannerPicArray: [],
      recommend: [],
      floor1: [],
      floor2: [],
      floor3:[],
      floorName:{},
      hotGoods:[],
      locationlIcon: require("../assets/images/location.png")
    };
  },
  components: {
    swiper,
    swiperSlide,
    floorComponent,
    goodsInfo
  },
  created() {
    this.$axios.get("users/IndexGoods")
      .then(res => {
       // console.log('res :', res.data[0]);
        if (res.status == 200) {
         
          this.category = res.data[0].category;
          this.adBanner = res.data[0].advertesPicture.PICTURE_ADDRESS;
          this.bannerPicArray = res.data[0].slides;
          this.recommend = res.data[0].recommend;
          
          this.floor1 = res.data[0].floor1;
          this.floor2 = res.data[0].floor2;
          this.floor3 = res.data[0].floor3;  
          this.floorName = res.data[0].floorName
          this.hotGoods = res.data[0].hotGoods
       
        }
      })
      .catch(err => {
      //  console.log(err);
      });
  }
};
</script>

<style >
.search-bar {
  background-color: #e16531;
  color: #e16531;
  height: 2.2rem;
  line-height: 2.2rem;
  background-color: #fff;
}
.search-icon {
  margin: 5px 0 0 5px;
}
.search-input {
  width: 100%;
  height: 1.3rem;
  border-top: 0px;
  border-left: 0px;
  border-right: 0px;
  border-bottom: 1px solid #e16531 !important;
  background-color: #fff;
  color: #fff;
}
.search-button {
  border-radius: 1rem;
  background-color: #e16531;
}
.swiper-area {
  clear: both;
  max-height: 15rem;

  overflow: hidden;
}
.type-bar {
  background-color: #fff;
  margin: 0 0.3rem 0.3rem 0.3rem;
  border-radius: 0.3rem;
  font-size: 14px;
  display: flex;
  flex-direction: row;
  /*  */
  flex-wrap: nowrap;
}
.type-bar div {
  padding: 0.3rem;
  font-size: 12px;
  text-align: center;
}
.recommend-area {
  background-color: #fff;
  margin-top: 0.3rem;
}
.recommend-title {
  border-bottom: 1px solid #ffffff;
  font-size: 14px;
  padding: 0.2rem;
  color: #e16531;
}
.recommend-body {
     border-top: #ddd 1px solid;
  border-bottom: 1px solid #eee;
}
.recommend-item {
  width: 90%;
  border-right: 1px solid #eee;
  font-size: 12px;
  text-align: center;
}
.hot-area{
  background-color: #ddd
}
.hot-title{
  background-color: #eee;
  font-size: 14px;
  line-height: 1.8rem;
  height: 1.8rem;
  text-align: center
}
.hot-goods{
  background-color: #fff;
 
}
</style>


