<template>
  <div class="editAdress">
    <van-nav-bar title="" left-arrow @click-left="onClickLeft" right-text="" />
    <van-cell-group>
      <van-field v-model="username" placeholder="" />
      <van-field v-model="phone" placeholder="" />
      <van-cell :title="address" is-link @click="editAdress" />
      <textarea v-model="detailAdress" placeholder="" class="address" />
    </van-cell-group>
    <van-popup v-model="show" position="bottom" :style="{ height: '40%' }">
      <van-area
        :area-list="areaList"
        :columns-placeholder="['', '', '']"
        :value="value"
        @confirm="sure()"
        @change="onChange"
      />
    </van-popup>
  </div>
</template>

<script>
import areaData from '../json/area';
export default {
  data() {
    return {
      value: "120105",
      areaList: areaData,
      username: "",
      show: false,
      phone: "",
      address: "",
      message: "",
      detailAdress: ""
    };
  },
  created() {
   // console.log("this.$router.query.item :", this.$route.query.item);
    this.username = this.$route.query.item.name;
    this.phone = this.$route.query.item.tel;
    this.address = this.$route.query.item.province+this.$route.query.item.city+this.$route.query.item.county;
    this.detailAdress = this.$route.query.item.address;
    this.value = this.$route.query.item.areaCode
  },
  methods: {
    onChange(picker) {
     
      this.message = picker.getValues();
    },
    sure() {
     

      let new_arr_code = this.message.map(obj => {
        return obj.code;
      });
      let new_arr_name = this.message.map(obj => {
        return obj.name;
      });
      this.address = new_arr_name[0] + new_arr_name[1] + new_arr_name[2];
      this.value = new_arr_code[2];
    //  console.log('this.value :', this.value);

      this.show = false;
    },
    editAdress() {
      this.show = true;
    },
    onClickLeft() {
      this.$router.go(-1);
    }
  }
};
</script>

<style scoped>
.address {
  width: 100%;
  border-color: #fff;
  font-size: 14px;
  margin-left: 0.8rem;
}
</style>


