<template>
  <div>
    <van-nav-bar title="" left-arrow @click-left="onClickLeft" />
    <van-address-edit
      :area-list="areaList"
      show-postal
      show-delete
      show-set-default
      show-search-result
      :search-result="searchResult"
      :area-columns-placeholder="['', '', '']"
      @save="onSave"
      @delete="onDelete"
      @change-detail="onChangeDetail"
    />
  </div>
</template>

<script>
import { mapState } from "vuex";
import areaData from "../json/area";
import { Toast } from "vant";
export default {
  data() {
    return {
      addressInfo: "",
      areaList: areaData,
      searchResult: []
    };
  },
  computed: {
    ...mapState({
      userAddress: state => state.userAddress
    })
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    onSave(content) {
      let id;
      if (this.userAddress == 0) {
        id = "1";
      } else {
        id = this.userAddress.length + 1 + "";
      }

      this.$store.dispatch("addAddress", {
        id: id,
        name: content.name,
        tel: content.tel,
        province: content.province,
        city: content.city,
        county: content.county,
        areaCode: content.areaCode,
        addressDetail: content.addressDetail,
        address:
          content.province +
          content.city +
          content.county +
          content.addressDetail,
        isDefault: content.true,
        areaCode: content.areaCode
      });
      Toast.success("");
      this.$router.go(-1);
    //  console.log("content) :", content);
    },
    onDelete() {
      Toast("delete");
    },

    onChangeDetail(val) {
      //   if (val) {
      //     this.searchResult = [
      //       {
      //         name: "",
      //         address: ""
      //       }
      //     ];
      //   } else {
      //     this.searchResult = [];
      //   }
    }
  }
};
</script>

<style scoped>
</style>


