<template>
  <div>
    <div class="main-div">
      <keep-alive>
         <router-view/>
      </keep-alive>
     
    </div>
    <van-tabbar v-model="active" @change="changeTabbar(active)">
      <van-tabbar-item icon="shop"></van-tabbar-item>
      <van-tabbar-item icon="records"></van-tabbar-item>
      <van-tabbar-item icon="cart"></van-tabbar-item>
      <van-tabbar-item icon="contact"></van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  data() {
    return{
        active: 0,
        nowPath:''
    }
  },
  created(){
  this.changeTabBarActive()
  },
  updated(){
     this.changeTabBarActive()
  },
  methods:{
    changeTabBarActive(){
      this.nowPath = this.$route.path
      console.log( this.nowPath);
      if (this.nowPath == '/ShopCar') {
          this.active = 2
      }
      if (this.nowPath == '/CategoryList') {
          this.active = 1
      }
      if (this.nowPath == '/Home') {
          this.active = 0
      }
      if (this.nowPath == '/Member') {
          this.active = 3
      }
    //  console.log( this.nowPath);
      
    
    },
    changeTabbar(active){
     
      switch(active){
        case 0:
          this.$router.push({name:'Home'})
          break;
        case 1:
          this.$router.push({name:'CategoryList'})
          break;
        case 2:
          this.$router.push({name:'ShopCar'})
          break;
        case 3:
          this.$router.push({name:'Member'})
          break;

      }
    }
  }
};
</script>

<style >
</style>


