import Vue from 'vue';
import Vuex from 'vuex';


Vue.use(Vuex);

const types = {
    SET_IS_AUTNENTIATED: 'SET_IS_AUTNENTIATED', // 
    SET_USER: 'SET_USER', // 
    TOKEN: ''    //token
}
const state = {
    selectGoods: [],
    userAddress:[],//
    isAutnenticated: false,  // 
    user: {},   // 
    token: '',   //token
    count: 0
}
const getters = {
    isAutnenticated: state => state.isAutnenticated,
    user: state => state.user,
    token: state => state.token,
    count: state => state.count,
}
const mutations = {

    [types.SET_IS_AUTNENTIATED](state, isAutnenticated) {
        if (isAutnenticated)
            state.isAutnenticated = isAutnenticated
        else
            state.isAutnenticated = false
    },
    [types.SET_USER](state, user) {
        if (user)
            state.user = user
        else
            state.user = {}
    },
    increment(state) {
        console.log('+1 :', "+1");
        state.count++;
        console.log('state.count :', state.count);
    },
    decrement(state) {
        state.count--;
    },
   

    [types.TOKEN](state, token) {
        if (token)
            state.token = token
        else
            state.token = ''
    },

    add(state, data) {

        state.selectGoods.push(data)
    },
    remove(state, data) {
        state.selectGoods.splice(data, 1);
    },
    addAddress(state, data) {

        state.userAddress.push(data)
    },
    removeAddress(state, data) {
        state.userAddress.splice(data, 1);
    }
}

const actions = {
    addGoods({ commit }, data) {
        commit('add', data)
    },
    removeGoods({ commit }, data) {
        commit('remove', data)
    },
    addAddress({ commit }, data) {
        commit('addAddress', data)
    },
    removeAddress({ commit }, data) {
        commit('removeAddress', data)
    },
    setIsAutnenticated: ({ commit }, isAutnenticated) => {
        commit(types.SET_IS_AUTNENTIATED, isAutnenticated)
    },
    setUser: ({ commit }, user) => {
        commit(types.SET_USER, user)
    },
    setToken: ({ commit }, token) => {
        commit(types.TOKEN, token)
    },

    increment({commit,state}){
        commit('increment'); //increment
     },
    decrement({commit,state}){
            commit('decrement');
    },
    clearCurrentState: ({ commit }) => {
        commit(types.SET_IS_AUTNENTIATED, false)
        commit(types.SET_USER, null)
    }
}

export default new Vuex.Store({
    state,
    mutations,
    actions,
    getters

});


