<template>
    <div class="goods-info">
        <div class="goods-image">
            <img v-lazy = "goodsImage" width="100%" alt="">
        </div>
        <div class="goods-name">{{goodsName}}</div>
        <div class="goods-price">{{goodsPrice}}</div>
    </div>
</template>

<script>
    export default {
        props:['goodsImage','goodsName','goodsPrice']
    }
</script>

<style>
.goods-info{
  
    border-right: 1px solid #eeeeee;
    border-bottom: 1px solid #eee;
}
.goods-name{


    
   padding-left: .8rem;
   width: 80%;
    font-size: 12px;
    overflow: hidden;
    
    text-align: center;
}

.goods-price{
    color: #e0620d;
    font-size: 14px;
    text-align: center;

}

</style>


