<template>
  <div>
    <div class="navbar-div">
      <van-nav-bar title="" class="navbar-div" />
    </div>
    <van-row>
      <van-col span="6">
        <div id="leftNav">
          <ul>
            <li
              @click="clickCategory(index,item.ID)"
              :class="{categoryActice:categoryIndex==index}"
              v-for="(item , index) in category"
              :key="index"
            >{{item.MALL_CATEGORY_NAME}}</li>
          </ul>
        </div>
      </van-col>
      <van-col span="18">
        <div class="tabCategorySub">
          <van-tabs
            v-model="active"
            @click="onClickCategorySub"
            line-height="1px"
            animated
            :ellipsis="false"
            title-active-color="rgb(238, 98, 4)"
          >
            <van-tab v-for="(item,index) in categorySub" :key="index" :title="item.MALL_SUB_NAME"></van-tab>
          </van-tabs>
        </div>
        <div id="list-div">
          <van-pull-refresh v-model="isRefresh" @refresh="onRefresh">
            <van-list v-model="loading" :finished="finished" @load="onLoad">
              <div
                class="list-item"
                @click="goGoodsInfo(item.ID)"
                v-for="(item,index) in goodList"
                :key="index"
              >
                <div class="list-item-img">
                  <img :src="item.IMAGE1" width="100%" :onerror="errorImg" />
                </div>
                <div class="list-item-text">
                  <div>{{item.NAME}}</div>
                  <div>{{item.ORI_PRICE}}</div>
                </div>
              </div>
            </van-list>
          </van-pull-refresh>
        </div>
      </van-col>
    </van-row>
  </div>
</template>

<script>
import axios from "axios";
import url from "@/serviceAPI.config.js";
export default {
  data() {
    return {
      categoryIndex: 0,
      categorySub: [], //
      goodList: [], //
      categorySubId: "", //Iid
      category: "",
      active: 0, //
      list: [],
      page: 1,
      loading: false,
      finished: false,
      isRefresh: false, //
      errorImg: 'this.src="' + require("@/assets/images/errorimg.png") + '"'
    };
  },
  created() {
    this.getCategory();
  },
  mounted() {
    let winHeight = document.documentElement.clientHeight;
    document.getElementById("leftNav").style.height = winHeight - 46 + "px";
    document.getElementById("list-div").style.height = winHeight - 140 + "px";
  },
  methods: {
    onRefresh() {
      setTimeout(() => {
        this.isRefresh = false;
        this.list = [];
        this.onLoad();
      }, 500);
    },
    onLoad() {
      setTimeout(() => {
        this.categorySubId = this.categorySubId
          ? this.categorySubId
          : this.categorySub[0].ID;
        this.getGoodsList();
      }, 1000);
    },
    clickCategory(index, categoryId) {
      this.getCategorySubByCategoryID(categoryId);
      this.page = 1;
      this.finished = false;
      this.goodList = [];
      this.categoryIndex = index;
    },
    getCategorySubByCategoryID(categoryId) {
       this.$axios.post("goods/getCategorySubList",{categoryId: categoryId})
        .then(response => {
          //console.log('response :', response);
          if (response.data.code == 200 && response.data.message) {
            this.categorySub = response.data.message;
            this.active = 0;
            this.categorySubId = this.categorySub[0].ID;
            this.onLoad();
          }
        })
        .catch(error => {
          //console.log(error);
        });
    },
    getCategory() {
      this.$axios.get("goods/getCategoryList")
        .then(response => {
        //  console.log("getCategoryList:",response);

          if (response.data.code == 200 && response.data.message) {
            this.category = response.data.message;
            this.getCategorySubByCategoryID(this.category[0].ID);
          //  console.log(this.category);
          } else {
            Toast("");
          }
        })
        .catch(error => {
         // console.log(error);
        });
    },
    getGoodsList() {
     
       this.$axios.post("goods/getGoodsListByCategorySubID",{ categorySubId: this.categorySubId,page: this.page})
        .then(response => {
       //  console.log('getGoodsListByCategorySubID:', response);
          if (response.data.code == 200 && response.data.message.length) {
            this.page++;
            this.goodList = this.goodList.concat(response.data.message);
          } else {
            this.finished = true;
          }
          this.loading = false;
        })
        .catch(error => {
         // console.log(error);
        });
    },
    onClickCategorySub(index, title) {
      this.categorySubId = this.categorySub[index].ID;
      this.goodList = [];
      this.finished = false;
      this.page = 1;
      this.onLoad();
    },
    //
    goGoodsInfo(id) {
    //  console.log(id);
       localStorage.setItem("goodsId", id);
      this.$router.push({ name: "Goods", params: { goodsId: id } });
    }
  }
};
</script>

<style scoped>
#leftNav {
  background-color: aliceblue;;
}
#leftNav ul li {
  line-height: 2rem;
  border-bottom: 1px solid #e4e7ed;
  padding: 3px;
  font-size: 0.8rem;
  text-align: center;
}
.navbar-div{
  background-color: rgb(238, 98, 4)
}
.categoryActice {
  background-color: rgb(255, 255, 255);
}

.list-item {
  display: flex;
  flex-direction: row;
  font-size: 0.8rem;
  border-bottom: 1px solid #f0f0f0;
  background-color: #fff;
  padding: 5px;
}
#list-div {
  overflow: scroll;
}
.list-item-img {
  flex: 8;
}
.list-item-text {
  flex: 16;
  margin-top: 10px;
  margin-left: 10px;
}
</style>


