<template>
  <div>
    <van-nav-bar title="" left-arrow @click-left="goBack" />

    <div class="register-panel">
      <van-field
        v-model="username"
        label=""
        icon="clear"
        clearable
        placeholder=""
        required
        @click-icon="username = ''"
      />

      <van-field v-model="password" type="password" label="" placeholder="" required />
      <div class="register-button">
        <van-button
          color="linear-gradient(to right, #4bb0ff, #6149f6)"
          round
          class="button"
          :loading="openLoading"
          @click="registeAction"
        ></van-button>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
import axios from "axios";
export default {
  data() {
    return {
      username: "",
      password: "",
      openLoading: false //Loading
    
    };
  },
  methods: {
    checkForm() {
      let isOK = true;
      if (this.username.length < 2) {
        Toast.fail("3");
        isOK = false;
      }
      if (this.password.length < 6) {
        Toast.fail("6");
        isOK = false;
      }
      return isOK;
    },
    goBack() {
      this.$router.go(-1);
    },
    registeAxios() {
      this.openLoading=true;

        this.$axios.post("users/register", {userName: this.username,password: this.password})
        .then(response => {
          if (response.data.status == 200) {
            Toast.success("");
          // this.username=''
          // this.password=''
            this.openLoading = false;
             this.$router.go(-1);
          } else {
            Toast.fail("");
            this.openLoading = false;
          }
        })
        .catch(error => {
          Toast.fail("");
          this.openLoading = false;
        });
    },
    registeAction() {
        this.checkForm() && this.registeAxios()
    }
  }
};
</script>

<style scoped>
.register-panel {
  width: 96%;
  border-radius: 5px;
  margin: 20px auto;
  padding-bottom: 50px;
}
.register-button {
  padding-left: 35px;
  padding-top: 20px;
}
.button {
  height: 80%;
  width: 80%;
}
</style>


