<template>
  <div class="body">
    <van-nav-bar title="" left-arrow @click-left="goBack" />
    <div class="address">
      <div class="empty" v-if="!show">
        <van-row>
          <van-col span="6">
            <img src="../assets/images/add.png" class="add-img" @click="add_address" />
          </van-col>
          <van-col span="8" class="add-txt"></van-col>
        </van-row>
      </div>
      <div class="no-empty" v-else>
        <van-row>
          <van-col span="4" class="username">{{name}}</van-col>
          <van-col span="8" class="phone">{{tel}}</van-col>
        </van-row>
        <van-row>
          <van-col class="detailAddress">{{chosenAddressId}}</van-col>
        </van-row>
      </div>
    </div>
    <div class="cart-list">
      <div v-if="change">
        <div class="pang-row">
          <div class="pang-img">
            <img :src="goodList.smpic" width="100%" />
          </div>
          <div class="pang-text">
            <div class="pang-goods-name">{{goodList.name}}</div>
            <div class="pang-control">
              <van-stepper v-model="goodList.count" />
            </div>
          </div>
          <div class="pang-goods-price">
            <div>{{goodList.price}}</div>
            <div class="count">x{{goodList.count}}</div>
          </div>
        </div>
      </div>
      <div v-else>
        <div class="pang-row" v-for="(item,index) in goodList" :key="index">
          <div class="pang-img">
            <img :src="item.smpic" width="100%" />
          </div>
          <div class="pang-text">
            <div class="pang-goods-name">{{item.name}}</div>
            <div class="pang-control">
              <van-stepper v-model="item.count" />
            </div>
          </div>
          <div class="pang-goods-price">
            <div>{{item.price}}</div>
            <div class="count">x{{item.count}}</div>
          </div>
        </div>
      </div>
    </div>
    <van-submit-bar :price="totalMoney" button-text="" @submit="onSubmit" class="submit-bar"></van-submit-bar>
    <van-popup v-model="popup_show" position="bottom" :style="{ height: '55%' }">
      <van-nav-bar title="" left-arrow @click-left="goBack" />
      <div class="paytotal">{{totalMoney/100}}</div>
      <div>
        <van-row >
          <van-col span="8" class="payType" ></van-col>
      
          <van-col span="8" >
            <van-dropdown-menu > 
              <van-dropdown-item v-model="value1" :options="option1"  />
            </van-dropdown-menu>
          </van-col>
        </van-row>
      </div>
      <van-button type="info" class="bottom-button"></van-button>
    </van-popup>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { Col } from "vant";
export default {
  data() {
    return {
      value1: 0,
      option1: [
        { text: "", value: 0 },
        { text: "", value: 1 },
        { text: "", value: 2 }
      ],
      show: false,
      change: false,
      popup_show: false,
      goodList: [],
      name: "CYJ",
      tel: "13058487358",
      totalPrice: 666,
      chosenAddressId: "1"
    };
  },
  updated() {
  //  console.log(11);
  },
  created() {
  //  console.log("222 :", 222);
  //  console.log("this.userAddress :", this.userAddress[0]);

    if (localStorage.url == "shopCar") {
      this.goodList = JSON.parse(localStorage.getItem("payGoods"));
      this.change = false;
      localStorage.removeItem("goodsPay");
    } else {
      this.goodList = JSON.parse(localStorage.getItem("goodsPay"));
      this.change = true;
      localStorage.removeItem("payGoods");
    }
    if (this.userAddress[0]) {
      this.name = this.userAddress[0].name;
      this.tel = this.userAddress[0].tel;
      this.chosenAddressId = this.userAddress[0].address;
      this.show = true;
    }

   // console.log("localStorage.url :", localStorage.url);
  },
  computed: {
    ...mapState({
      userAddress: state => state.userAddress
    }),
    totalMoney() {
      let allMoney = 0;
      if (localStorage.url == "shopCar") {
        this.goodList.forEach((item, index) => {
          allMoney += item.price * item.count * 100;
        });
      } else {
        allMoney = this.goodList.count * this.goodList.price * 100;
      }

      return allMoney;
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    onSubmit() {
      this.popup_show = true;
    },
    add_address() {
      this.$router.push("/AddAddress");
    }
  }
};
</script>
<style scoped>
/* .body{
  background-color: #e6e6e6
} */
.add-img {
  width: 40%;
  height: 40%;
}
.add-txt {
  margin-top: 0.2rem;
  color: #1195db;
}
.address {
  margin: 0.6rem;
  background-color: #fff;
  text-align: center;
  width: 18rem;
  padding: 0.5rem;
  border-radius: 0.5rem;
  /* box-shadow: 0px 0px 0px 0px green; */
  /* box-shadow: 10px 5px 0px 0px green; */
  /* box-shadow: -10px -5px 0px 0px green; */
  /* box-shadow: 10px 10px 10px 0px green; */
  box-shadow: 2px 2px 5px 4px #e6e6e6;
}
.username {
  font-size: 14px;
  margin-left: 1rem;
}
.phone {
  padding: 5px;
  font-size: 12px;
  color: darkgray;
}
.detailAddress {
  font-size: 14px;
  margin-left: 1rem;
}

.cart-list {
  background-color: #e6e6e6;
}
.pang-row {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  padding: 0.5rem;
  font-size: 0.85rem;
  border-bottom: 1px solid #fff;
}

.pang-img {
  flex: 6;
}
.pang-text {
  flex: 14;
  padding-left: 10px;
}
.pang-control {
  padding-top: 10px;
}
.pang-goods-price {
  flex: 5;
  text-align: right;
}
.count {
  padding-top: 1rem;
}
.submit-bar {
  border-top: 1px solid #e6e6e6;
}
.paytotal {
  margin-top: .2rem;
  text-align: center;
  font-size: 18px;
}
.payType {
  margin-top: 1rem;
  margin-left: 1rem;
  margin-right: 4rem;
  font-size: 12px;
}
.bottom-button {
  position: fixed;
  left: 0px;
  bottom: 0px;
  width: 100%;
  height: 3rem;
  background-color: #1989FA;
  z-index: 9999;
}
</style>


