<template>
  <div>
    <van-nav-bar title="" left-arrow @click-left="goBack" />

    <div class="register-panel">
      <van-field
        v-model="username"
        label=""
        icon="clear"
        clearable
        placeholder=""
        required
        @click-icon="username = ''"
      />

      <van-field v-model="password" type="password" label="" placeholder="" required />
    </div>
    <div class="register-button">
      <van-button
        color="linear-gradient(to right, #4bb0ff, #6149f6)"
        round
        class="button"
        @click="login"
      ></van-button>
    </div>
    <div class="bottom-space">
      <van-row type="flex" justify="space-between">
        <van-col span="6">?</van-col>
        <van-col span="6" @click="register"></van-col>
      </van-row>
    </div>
  </div>
</template>

<script>
import jwt_decode from "jwt-decode";
import { Toast, Col } from "vant";
import axios from "axios";
export default {
  data() {
    return {
      username: "",
      password: "",
      openLoading: false //Loading
    };
  },
  computed: {
    // ...mapState({
    //   userToken: state => state.userToken //store.userToken
    // })
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    checkForm() {
      let isOK = true;
      if (this.username.length < 3) {
        Toast.fail("3");
        isOK = false;
      }
      if (this.password.length < 6) {
        Toast.fail("6");
        isOK = false;
      }
      return isOK;
    },
    register() {
      this.$router.push("/Register");
    },
    loginAxios() {
      this.openLoading = true;
      this.$axios
        .post("users/login", {
          userName: this.username,
          password: this.password
        })
        .then(res => {
         // console.log(res.data);
          if (res.data.status == 200) {
            const { token } = res.data;
            const decode = jwt_decode(token);
            this.$store.dispatch("setIsAutnenticated", !this.isEmpty(decode));
            this.$store.dispatch("setUser", decode);
            this.$store.dispatch("setToken", token);
            Toast.success("");
            this.openLoading = false;
           
          this.$router.go(-1);
          } else {
            Toast.fail("");
            this.openLoading = false;
          }
        })
        .catch(error => {
         // console.log("error :", error);
          Toast.fail("");
          this.openLoading = false;
        });
      // axios({
      //   url: "http://localhost:3000/api/users/login",
      //   method: "post",
      //   data: {
      //     userName: this.username,
      //     password: this.password
      //   }
      // })
      //   .then(response => {
      //     console.log('response :', response);
      //     if (response.data.status == 200) {
      //       Toast.success("");
      //       this.openLoading = false;
      //       // this.$router.go(-1);
      //     } else {
      //       Toast.fail("");
      //       this.openLoading = false;
      //     }
      //   })
      //   .catch(error => {
      //     console.log('error :', error);
      //     Toast.fail("");
      //     this.openLoading = false;
      //   });
    },
    isEmpty(value) {
      return (
        value === undefined ||
        value === null ||
        (typeof value === "object" && Object.keys(value).length === 0) ||
        (typeof value === "string" && value.trim().length === 0)
      );
    },
    login() {
      this.checkForm() && this.loginAxios();
    }
  }
};
</script>

<style scoped>
.register-panel {
  width: 96%;
  border-radius: 5px;
  margin: 20px auto;
  padding-bottom: 10px;
}
.register-button {
  padding-left: 35px;
}
.button {
  height: 80%;
  width: 80%;
}
.bottom-space {
  padding-left: 10px;
  padding-top: 10px;
  font-size: 12px;
}
</style>


